import FFT from "@sigilnet/fft-ts/core/fft-base/fft";

export interface FFTImplementation {
  forward(input: number[]): { real: number[]; imag: number[] };
  inverse(real: number[], imag: number[]): number[];
}

export const NodeFFT: FFTImplementation = {
  forward(input) {
    const processor = new FFT(input.length);
    const out = processor.createComplexArray();
    const inComplex = processor.createComplexArray();

    for (let i = 0; i < input.length; i++) {
      inComplex[2 * i] = input[i];
      inComplex[2 * i + 1] = 0;
    }

    processor.transform(out, inComplex);

    const N = input.length;
    const real = new Array(N);
    const imag = new Array(N);

    for (let i = 0; i < N; i++) {
      real[i] = out[2 * i];
      imag[i] = out[2 * i + 1];
    }
    return { real, imag };
  },
  inverse(real, imag) {
    const N = real.length;
    const processor = new FFT(N);
    const inComplex = processor.createComplexArray();
    for (let i = 0; i < N; i++) {
      inComplex[2 * i] = real[i];
      inComplex[2 * i + 1] = imag[i];
    }
    const out = processor.createComplexArray();
    processor.inverseTransform(out, inComplex);
    const result = new Array(N);
    for (let i = 0; i < N; i++) {
      result[i] = out[2 * i];
    }
    return result;
  },
};
